@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Mulish', sans-serif;
}

.navbar {
    background-color: #fff !important;
}

.navbar-brand{
    font-size: 1.9rem !important;
    color: #565387 !important;
}

.navbar a {
    font-size: 1.3rem;
    text-transform: capitalize;
}

.navbar a:hover {
    color: #3498db !important;
}

.menu_active {
    font-weight: bold;
    border-bottom: 1px solid #565387;
}

@media (max-width: 991px){
    .navbar-brand {
        font-size: 1.5rem !important;
    }

    .menu_active {
        font-weight: bold;
        border-bottom: none;
    }
}

#header {
    width: 100%;
    height: 80vh;
}

#header h2 {
    font-size: 24px;
    color: #484848;
}

#header h1 {
    margin: 0 0 10px 0;
    font-size: 48px;
    font-weight: 700;
    color: black;
}

#header .brand-name {
    color: #3498db;
}

#header .btn-know-more {
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    pad: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color: #3498db;
    border: 2px solid #3498db;
    text-decoration: none;
}

#header .btn-know-more:hover {
    background: #3498db;
    color: #fff;
}

.header-img {
    text-align: right;
}

#header .animated {
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
    0% {
        transform: translateY(10px);
    }
    100% {
        transform: translateY(-10px);
    }
}

@keyframes up-down {
    0% {
        transform: translateY(10px);
    }
    100% {
        transform: translateY(-10px);
    }
}

.card-title {
    font-size: 1rem !important;
}

.card-text {
    font-size: 0.8rem !important;
}
 .brand-name {
    color: #3498db;
}

.carousel-control-prev-icon,
.carousel-control-next-icon {
  height: 100px;
  width: 100px;
  outline: none;
  background-size: 100%, 100%;
  border-radius: 50%;
  border: none;
  background-image: none;
}

.carousel-control-next-icon:after
{
  content: '>';
  font-size: 55px;
  color: red;
  opacity: 50%;

}

.carousel-control-prev-icon:after {
  content: '<';
  font-size: 55px;
  color: red;
  opacity: 50%;
}

.fa{
    font-size: 45px;
    color: #3498db;
}

@media (min-width: 991px){
    .hr{
          display: none;
    }
}

@media (max-width: 991px){
    #sec {
        width: 100%;
        height: 100%;
        margin-bottom: 70px;
    }
    #header {
        width: 100%;
        height: 100%;
        margin-bottom: 70px;
    }
}


